.notification{
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius:5px;
  padding: 10px;
  margin-bottom:10px;
}
.error {
  color: red;
}

.confirmation {
  color: green;
}

button {
  border-radius: 5px;
  padding: 0.2em 1em;
  border:none;

}